.blog-entry-root{border:1px solid #a9a9a9;background-color:#fff;border-radius:20px;padding:15px;margin-top:20px;display:-webkit-box;display:-moz-box;display:-ms-flexbox;display:-moz-flex;display:-webkit-flex;display:flex;flex-direction:column}.blog-entry-root .blog-entry.title{color:#5d5d5d;font-size:26px;font-weight:bold;padding:10px 0}.blog-entry-root .blog-entry.date{color:#5d5d5d}.blog-entry-root .blog-entry.summary{color:#5d5d5d}.blog-entry-root .blog-entry.show-more{display:-webkit-box;display:-moz-box;display:-ms-flexbox;display:-moz-flex;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;align-items:center;align-self:flex-end;color:#5d5d5d;cursor:pointer}.blog-entry-root .blog-entry.show-more:hover{color:#ff3b3f;fill:#ff3b3f}.blog-entry-root svg{width:22px;height:22px}



